import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ICompany } from 'src/app/shared/interfaces/i-company';
import { NotificationService } from '../../../services/notification.service';
import { CompanyService } from '../services/company.service';

@Component({
  selector: 'app-company-dialog',
  templateUrl: './company-dialog.component.html',
  styleUrls: ['./company-dialog.component.scss']
})
export class CompanyDialogComponent implements OnInit {

  companyForm!: FormGroup;
  companyData!: ICompany;
  constructor(
    public dialogRef: MatDialogRef<CompanyDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private companySvc: CompanyService,
    private formB: FormBuilder,
    private notiSvc: NotificationService) { 
      this.buildForm()
    }

  ngOnInit(): void {
    if(this.data?.element){
      this.companyForm.patchValue(this.data.element);
    }
  }

  submitCompany() {
    if(this.companyForm.invalid){
      this.notiSvc.openSnackBar("Algunos de los campos son invalidos", 3000)
      return
    }
    this.setCompany()
    if(this.data?.element){
      this.companySvc.updateCompany(this.companyData).subscribe(
        val => {
          console.log(val)
          this.notiSvc.openSnackBar("Empresa "+ this.companyData.name+ " actualizada correctamente", 2000)
          this.dialogRef.close()
        },
        error => {
          console.log(error)
        }
      )
    } else {
      this.companySvc.createCompanie(this.companyData).subscribe(
        val => {
          console.log(val)
          this.notiSvc.openSnackBar("Empresa agregada correctamente", 2000)
          this.dialogRef.close()
        },
        error => {
          console.log(error)
        }
      )
    }
  }
  setCompany(): void {
    this.companyData = {
      ...this.companyForm.value,
      id: this.data?.element ? this.data?.element.id : null
    }
  }

  buildForm(): void {
    this.companyForm = this.formB.group({
      name: ['', Validators.required],
      active: ['', Validators.required]
    })
  }
}
