import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ICompany } from 'src/app/shared/interfaces/i-company';
import { NotificationService } from '../../services/notification.service';
import { CompanyDialogComponent } from './company-dialog/company-dialog.component';
import { CompanyService } from './services/company.service';

@Component({
  selector: 'app-company',
  templateUrl: './company.component.html',
  styleUrls: ['./company.component.scss']
})

export class CompanyComponent implements OnInit {
  displayedColumns: string[] = ['id', 'nombre', 'active', 'edit'];
  dataSource: MatTableDataSource<ICompany>;
  companys: ICompany[] = [
  ]

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private companySvc: CompanyService, 
              public dialog: MatDialog,
              private notiSvc: NotificationService) {
  }

  ngOnInit(): void {
    this.getCompanies();
  }


  openDialog(edit: boolean, element?: ICompany): void {
    const dialogRef = this.dialog.open(CompanyDialogComponent, {
      disableClose: false,
      data: {edit, element},
    }).afterClosed().subscribe((data: any) => {
      if(!data){
        this.getCompanies();
      }
    });
  }

  getCompanies(): void {
    this.companySvc.showCompanies().subscribe((data) => {
      this.companys = data['data']
      this.setData()
    })
  }


  setData(): void {
    this.dataSource = new MatTableDataSource();
    this.dataSource.data = this.companys;
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
      const filterValue = (event.target as HTMLInputElement).value;
      this.dataSource.filter = filterValue.trim().toLowerCase();

      if (this.dataSource.paginator) {
        this.dataSource.paginator.firstPage();
      }
    }
}
